.tags-h1 {
    font-weight: 400;
    margin-top: 50px;
}

.tags-p{
    margin: '0px';
    font-size: 15px;
}

.tag-list-container {
    padding: 30px 0px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
    gap: 10px;
}

.tag {
    padding: 10px;
    border: 1px solid #d2d2d2;
    border-radius: 2px;
}

.tag h5 {
    display: inline-block;   
    margin: 10px 0px;
    padding: 5px 5px;
    background-color: #e1ecf4;
    color: #39739d;
}

.tag p {
    font-size: 14px;
    color: #323232;
    line-height: 17px;
    
}