.main-bar{
    float: left;
    margin: 24px 0;
    width: calc(100% - 300px - 24px );
    padding: 0;
}

.main-bar-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main-bar-header h1{
    font-weight: 400;
}

.main-bar-header .ask-btn{
    padding: 10px;
    border-radius: 4px;
    background-color: #009dff;
    color: white;
    border: none;
    text-decoration: none;
    transition: 0.3s;
    cursor: pointer;
}

.main-bar-header .ask-btn:hover{
    background-color: #0086d8;
}

.display-question-conatiner{
    min-height: 80px;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: #fdf7e2;
    border-bottom: 1px solid #edeff0;
}

.display-question-conatiner .display-votes-ans {
    padding: 20px;
}

.display-question-conatiner .display-votes-ans p{
    margin: 0;
    text-align: center;

}

.display-question-details{
    flex-grow: 1;
    padding: 0 20px;
}

.question-title-link{
    text-decoration: none;
    color: #037ecb;
    transition: 0.3s;
}

.question-title-link:hover{
    color: #009dff;
}

.display-question-details p{
    padding: 4px;
    margin: 0%;
}

.display-tags-time .display-tags{
    display: flex;
    flex-wrap: wrap;
}

.display-tags-time{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

.display-tags-time .display-tags p{
    padding: 4px;
    margin: 4px;
    font-size: 13px;
    background-color: #edeff0;
    color: #39739d;
}

.display-tags-time .display-time{
    font-size: 13px;
}