.user-detailes-container {
    width: 100%;
    margin-top: 50px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.user-details {
    display: flex;
    align-items: flex-start;
}

.user-name{
    padding-left: 20px;
}

.user-name p {
    color: #7e7e7e;
}

.edit-profile-btn {
    padding: 8px 10px;
    border: 1px solid #7e7e7e;
    border-radius: 2px;
    background-color: white;
    cursor: pointer;
    transition: 0.3s;
}

.edit-profile-btn:hover {
    background-color: #f9fcfc;
}

.edit-profile-title {
    padding: 20px 0px;
    border-bottom: solid 1px #dbd9d9;
}

.edit-profile-title-2 {
    color: gray;
    font-weight: 400;
}

.edit-profile-form {
    padding: 20px;
    border: 1px solid #dbd9d9;   
    border-radius: 5px;
}

.edit-profile-form label h3 {
    margin: 0%;
    padding: 3px 0px;
}

.edit-profile-form label input, .edit-profile-form label textarea {
    padding: 5px;
    margin-bottom: 20px;
    border: 1px solid #dbd9d9;
    width: 50%;
}

.user-submit-btn {
    padding: 10px 10px;
    background-color: #0a95ff;
    color: white;
    border: none;
    border-radius: 2px;
    transition: 0.2s;
    cursor: pointer;
}

.user-submit-btn:hover {
    background-color: #0074cc;
}

.user-cancle-btn {
    padding: 10px 10px;
    color: #0a95ff;
    background-color: transparent;
    border: none;
    margin-left: 10px;
    cursor: pointer;
}

